import { store } from '../../background/src/background_data_store';

const GOOGLE_ANALYTICS_TRACKING_ID_ENUM = {
  prod: "UA-60584558-1",
  dev: "UA-136110580-1",
};

export const GOOGLE_ANALYTICS_TRACKING_ID =
    GOOGLE_ANALYTICS_TRACKING_ID_ENUM[__WEBPACK_BUILDMODE__];

export function getGACid() {
    let cid = store.getState().data.cid;
    if(cid != undefined) {
        return cid;
    }
    else {
        cid = Math.floor(Math.random() * 0x7FFFFFFF) + "." + Math.floor(Date.now() / 1000);
        store.dispatch({ type: 'SET_CID', data: cid });
    }
    return cid;
}
